.container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px;
}

.Search {
    height: 10vh;
    background-color: white;
    border: 3px solid #032988;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 20px;
    bottom: -25px;
    width: 100%;
    max-width: 1024px;
  }
  
  .headerIcon {
    color: lightgray;
  }
  
  .SearchItem {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
  }
  
  .SearchInput {
    border: none;
    outline: none;
  }

  .Search-btn{
    color: #fff;
    background-color: rgb(70, 124, 217);
    padding: 15px 20px;
    border-radius: 10px;
    border: none;
  }

  @media  screen and (max-width: 900px) {
    .container{
      padding: 50px 50px;
    }
    
    .Search {
      height: 40vh;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      padding: 20px;
      gap: 3rem;
    }
  }