.room-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem 8rem;
    gap: 4rem;
}

.room-container h1{
    font-size: 40px;
}

.room-grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 6vw;
    cursor: pointer;
}

.room img{
    border-radius: 10px;
}

.room h4, p{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media  screen and (max-width: 1050px){
    .room-grid{
        grid-template-columns: repeat(2, 1fr);
    }
}

@media  screen and (max-width: 700px){
    .room-container{
        padding: 2rem 4rem;
    }

    .room-grid{
        grid-template-columns: repeat(1, 1fr);
    }
}