.hero{
    display: flex;
    padding: 5rem 0;
    text-align: center;
    gap: 2rem;
    flex-direction: column;
    }

    .mainText{
      color: #131110;
      font-weight: 1000;
      font-size: 25px;
      line-height: 2.5rem;
    }
  
    .heroImgs{
      width: 100%;
      height: 1rem auto 0;
      justify-content: center;
      position: relative;
      border-radius: 5rem;
      display: flex;
      justify-content: center;
      align-items: center;

    }

    .videoDiv{
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;

      .video{
        width: 100%;
        height: 20%;
        border-radius: 50rem;
      }
    }

    .image{
      position: absolute;
      width: 20%;
    }

  @media  screen and (max-width: 1000px) {

    .mainText{
      font-size: 15px;
    }

    .videoDiv {
      width: 100%;
    }

    .image{
      display: none;
    }
  }
  
