.v-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    padding: 6rem 0;
    margin: 4rem 4rem;
}

.left{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img{
    border-radius: 3rem;
}

.right{
    width: 40%;
}

.right-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    gap: 0.5rem;
}

.right-header p {
    justify-content:flex-start;
}

.accordion{
    border: none;
}

.accordion-btn {
    color: #fff;
    background-color: rgb(70, 124, 217);
    padding: 20px;
    border-radius: 10px;
    display: flex;
    justify-content:space-between;
    align-items: center;
}

@media  screen and (max-width: 900px) {
    .v-container {
      flex-direction: column;
      align-items: center;
      padding: 0;
    }

    .left{
        width: 100%;
        margin-bottom: 4rem;
    }

    .right {
        width: 100%;
        gap: 15px;
    }
  }